#list-student {
  width: 100%;
  min-height: 100vh;
  height: auto;
  padding-left: 300px;
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
  justify-content: center;
}

#list-student section {
  max-width: 700px;
  width: 700px;
}

.teacher-item {
  background: var(--color-box-base);
  border: 1px solid var(--color-line-in-white);
  border-radius: 0.8rem;
  margin-top: 2.4rem;
  overflow: hidden;
}

.teacher-item header {
  padding: 3.2rem 4rem;
  display: flex;
  justify-content: flex-start;
}

.teacher-item header div h1 {
  font: 700 2.4rem Archivo;
  color: var(--color-text-title);
}

.teacher-item .main-student {
  padding: 0 4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.teacher-item footer {
  height: 6rem;
  background: rgba(0, 0, 0, .2);
  border-top: 1px solid var(--color-box-line-in-white);
  margin-top: 3.2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.teacher-item footer button {
  width: 50%;
  height: 100%;
  color: #FFF;
  cursor: pointer;
}

.teacher-item footer a {
  width: 50%;
  height: 100%;
  color: #FFF;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--color-primary);
  border: none;
  transition: all .5s;
}

.teacher-item footer button:last-child {
  background: red;
  border: none;
  transition: all .5s;
}

.teacher-item footer button:hover {
  opacity: .5;
}

.teacher-item footer a:hover {
  opacity: .5;
}